<template>
  <div class="buyActivityGoods mescroll">
    <van-tabs v-model="active" title-active-color="#ee0a24" @change="tabChange">
      <van-tab title="自提">
        <div class="address">
          <van-icon name="manager-o" size="20" color="#999" />
          <div class="text">
            <van-field v-model="username2" clearable placeholder="请输入提货人姓名" />
          </div>
          <van-icon name="phone-circle-o" size="20" color="#999" />
          <div class="text">
            <van-field v-model="usermobile2" clearable placeholder="请输入提货人手机号" />
          </div>
        </div>
      </van-tab>
      <van-tab title="送货">
        <div class="address" @click="$router.push({name:'myAddress',query:{type:1}})">
          <img class="left" src="../../assets/img/icon/dizhi.png" alt />
          <div class="mid">
            <div v-if="address">
              <p class="name">
                收货人:{{address.userName2}}
                <span>{{address.userMobile}}</span>
              </p>
              <p
                class="addr"
              >收货地址:{{(address.area?address.area.areaNames.replace(/\//g,''):'') + address.addrAddr}}</p>
            </div>
            <div v-else>
              <p style="color:#999;font-size:13px;">请添加收货地址</p>
            </div>
          </div>
          <img class="right" src="../../assets/img/icon/left.png" alt />
        </div>
      </van-tab>
    </van-tabs>

    <div class="line"></div>
    <div class="list">
      <div class="item">
        <div class="flex-goods">
          <div class="item-pic flex-1">
            <img :src="activeDetail.aimg?$utils.dealImg(activeDetail.aimg):''" />
          </div>
          <div class="flex-2">
            <div class="title">{{activeDetail.aname}}</div>
            <div class="flex info">
              <div class="price">￥{{activeDetail.aoutPrice?activeDetail.aoutPrice.toFixed(2):''}}</div>
            </div>
          </div>
        </div>
      </div>

      <van-cell-group v-if="activeDetail.aredTemplate == 2">
        <van-field
          label="您的电话"
          input-align="right"
          v-model="telno"
          placeholder="请输入您的电话号码"
          @blur="blur"
          @focus="focus"
        />
        <van-field
          label="车型品牌"
          input-align="right"
          v-model="brand"
          placeholder="请输入车型品牌"
          @blur="blur"
          @focus="focus"
        />
        <plateNumber @getPlateLicense="getPlateLicense" ref="plateNumber"></plateNumber>
      </van-cell-group>
      <div class="flex" style="margin:10px 0 0;">
        <div class="flex-1">备注</div>
        <div class="flex-2">
          <van-field
            v-model="remark"
            type="textarea"
            placeholder="备注"
            input-align="right"
            @blur="blur"
            @focus="focus"
          />
        </div>
      </div>
    </div>
    <div class="orderInfo" v-if="false">
      <div class="flex">
        <div class="flex-1">商品数量</div>
        <div class="flex-2">{{allNum}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">商品合计</div>
        <div class="flex-2">￥{{allPrice.toFixed(2)}}</div>
      </div>
      <div class="flex" v-if="discounts">
        <div class="flex-1">优惠合计</div>
        <div class="flex-2">￥{{discounts.toFixed(2)}}</div>
      </div>
      <div class="flex" v-if="freight">
        <div class="flex-1">物流合计</div>
        <div class="flex-2">￥{{freight.toFixed(2)}}</div>
      </div>
    </div>
    <div class="bottom">
      <div class="left">
        实付款：
        <span class="font">¥</span>
        <span class="price">{{activeDetail.aoutPrice?activeDetail.aoutPrice.toFixed(2):''}}</span>
      </div>
      <div class="btn" @click="submit1">确认订单</div>
    </div>
    <van-popup v-model="popupShow">
      <div class="popup_wrap">
        <div class="popup_icon">
          <img src="../../assets/img/icon/cha-3.png" />
        </div>
        <div class="popup_content">
          <div class="title">
            支付
            <span>{{text}}！</span>
          </div>
          <div class="text">您可以在15分钟内前往我的订单进行支付，超过时长的订单将失效</div>
        </div>
        <div class="popup_btns">
          <div class="popup_btn" @click="cancel">取消</div>
          <div class="popup_btn go" @click="go">立即前往</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Popup, Field, CellGroup, Cell, Tabs, Tab, Icon } from "vant";
import plateNumber from "@/components/plateNumber";
import { getAddressList, generateActivityOrder } from "@/api";
export default {
  components: {
    [Popup.name]: Popup,
    [Field.name]: Field,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Icon.name]: Icon,
    plateNumber
  },
  data() {
    return {
      active: 0,
      telno: "",
      brand: "",
      popupShow: false,
      text: "失败",
      remark: "",
      address: {},
      activeDetail: {},
      number: "",
      commonCard: "1",
      falfirstWrapStatusse: false,
      keyBoardStatus: false,
      username2: "",
      usermobile2: ""
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    focus() {
      this.$refs.plateNumber.firstWrapStatus = false;
      this.$refs.plateNumber.keyBoardStatus = false;
    },
    getPlateLicense(data, commonCard) {
      this.number = data;
      this.commonCard = commonCard;
    },
    submit1() {
      if (this.active == 1 && !this.address.addrCode) {
        this.$toast("请选择地址");
        return;
      }
      if (this.active == 0 && !this.username2) {
        this.$toast("请输入提货人姓名");
        return;
      }
      if (this.active == 0 && !this.usermobile2) {
        this.$toast("请输入提货人手机号");
        return;
      }
      if (this.active == 0 && !this.$utils.isMobile(this.usermobile2)) {
        this.$toast("请输入正确的手机号");
        return;
      }

      if (this.activeDetail.aredTemplate == 2 && !this.brand) {
        this.$toast("请输入车辆品牌");
        return;
      }
      if (this.activeDetail.aredTemplate == 2) {
        this.$refs.plateNumber.submitFn();
      }
      if (this.activeDetail.aredTemplate == 2 && !this.number) {
        this.$toast("请输入车牌号");
        return;
      }
      if (
        this.activeDetail.aredTemplate == 2 &&
        ((this.commonCard == "1" && this.number.length < 7) ||
          (this.commonCard == "2" && this.number.length < 8))
      ) {
        this.$toast("请输入正确的车牌号");
        return;
      }
      this.submit();
    },
    submit() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      let dataObj = {
        addressId: this.address.id,
        activityCode: this.activeDetail.acode,
        userCarBrand: this.brand,
        userCarNo: this.number,
        remarks: this.remark,
        username2: this.active == 0 ? this.username2 : "",
        usermobile2: this.active == 0 ? this.usermobile2 : ""
      };
      generateActivityOrder(dataObj).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.orderId = res.data.orderCode;
          sessionStorage.removeItem("addressId");
          if (!res.data.appId) {
            this.$toast("支付成功", "middle");
            this.$router.replace({
              name: "payResult",
              query: { id: this.orderId, type: 1 }
            });
          } else {
            this.weChatPay(
              res.data.appId,
              res.data.timeStamp,
              res.data.nonceStr,
              res.data.package,
              res.data.signType,
              res.data.paySign
            );
          }
        } else {
          this.$toast(res.msg);
        }
      });
    },
    weChatPay(appId, timeStamp, nonceStr, packageValue, signType, sign) {
      console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              // $this.$toast("支付成功", "middle");
              // setTimeout(() => {
              $this.$router.replace({
                name: "activityPayResult",
                query: {
                  id: $this.orderId,
                  type: 1,
                  shopCode: $this.activeDetail.shopCode
                }
              });
              // }, 500);
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              // $this.show = false;
              // $this.popupShow = true;
              // $this.text = "取消";
              // console.log("支付取消");
              $this.$router.replace({
                name: "activityPayResult",
                query: {
                  id: $this.orderId,
                  type: 2,
                  shopCode: $this.activeDetail.shopCode
                }
              });
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              // $this.show = false;
              // $this.popupShow = true;
              // $this.text = "失败";
              // $this.$toast("支付失败，请稍后再试", "middle");
              $this.$router.replace({
                name: "activityPayResult",
                query: {
                  id: $this.orderId,
                  type: 3,
                  shopCode: $this.activeDetail.shopCode
                }
              });
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    },
    cancel() {
      this.$router.back();
    },
    go() {
      this.$router.replace({
        name: "orderDetail",
        query: { id: this.orderId }
      });
    },
    loadData() {
      if (sessionStorage.getItem("address")) {
        this.address = JSON.parse(sessionStorage.getItem("address"));
      } else {
        const loading = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true // 禁用背景点击
        });
        getAddressList({
          pageNum: 1,
          pageSize: 10
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.address = res.data.list.length ? res.data.list[0] : {};
          } else {
            this.$toast.fail(res.msg);
          }
        });
      }
    }
  },
  activated() {
    document.title = "提交订单";
    this.activeDetail = JSON.parse(sessionStorage.getItem("active"));
    this.popupShow = false;
    this.loadData();
    this.username2 = sessionStorage.getItem("userName2");
    this.usermobile2 = sessionStorage.getItem("userMobile2");
  },
  beforeDestroy() {
    this.popupShow = false;
  }
};
</script>
<style lang="scss" scoped>
.buyActivityGoods {
  background: #fff;
  .address {
    display: flex;
    align-items: center;
    padding: 10px;
    .left {
      width: 20px;
      margin-right: 10px;
    }
    .mid {
      flex: 2;
      margin-right: 10px;
      .name {
        font-weight: bold;
        line-height: 30px;
        span {
          float: right;
        }
      }
      .addr {
        color: #999;
        font-size: 15px;
      }
    }
    .right {
      width: 10px;
    }
  }
  .line {
    border-bottom: 5px solid #eee;
  }
  .list {
    border-bottom: 1pt solid #efefef;
    .title {
      padding: 5px 10px;
      font-weight: bold;
      line-height: 30px;
    }
    .flex {
      display: flex;
      padding: 0 10px 10px;
      .flex-1 {
        flex: 1;
        color: #999;
      }
      .flex-2 {
        flex: 2;
        line-height: 25px;
      }
    }

    .item {
      // height: 120px;
      overflow: hidden;
      padding: 5px 10px;
      .flex {
        display: flex;
        align-items: center;
        padding: 0;
        .flex-2 {
          flex: 2;
        }
      }
      .flex-goods {
        display: flex;
        padding: 0;

        .flex-2 {
          flex: 2;
          position: relative;
        }
      }
      .del {
        width: 100%;
        margin: 50px auto 0;
      }
      .item-pic {
        margin-right: 10px;
        width: 120px;
        img {
          width: 100%;
          height: 120px;
        }
      }
      .title {
        font-size: 16px;
        line-height: 25px;
        padding: 0;
        max-height: 50px;
        font-weight: normal;
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
      }
      .parameter {
        color: #999;
        font-size: 15px;
        line-height: 20px;
        margin: 10px 0;
      }
      .info {
        position: absolute;
        bottom: 0;
        width: 100%;
        .price {
          font-size: 15px;
          font-weight: 600;
          color: #333;
          flex: 2;
        }
        .stepper {
          margin-right: 5px;
        }
      }
    }
  }
  .orderInfo {
    padding: 10px 15px 60px;
    .flex {
      line-height: 30px;
      color: #999;
      font-size: 16px;
      display: flex;
      .flex-2 {
        flex: 2;
        text-align: right;
        color: #333;
      }
    }
    .allPrice {
      text-align: right;
      font-size: 20px;
      font-weight: bold;
      color: #eb5252;
      span {
        font-size: 30px;
        font-weight: bold;
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    width: calc(100% - 10px);
    background: #fff;
    padding: 0 0 0 10px;
    display: flex;
    height: 50px;
    line-height: 50px;
    border-top: 1pt solid #efefef;
    .left {
      flex: 2;
      .font {
        color: #eb5252;
        font-size: 13px;
        margin-right: 3px;
      }
      .price {
        color: #eb5252;
        font-size: 18px;
        font-weight: bold;
      }
    }
    .btn {
      width: 120px;
      background: #eb5252;
      color: #fff;
      text-align: center;
      font-size: 13px;
    }
  }
  .van-popup {
    overflow-y: visible !important;
  }

  .popup-line div {
    background: #e9e9e9;
    width: 30px;
    height: 3px;
    margin: 10px auto 20px;
    border-radius: 3px;
  }

  .popup-content {
    padding: 0 15px;
    .content-top {
      text-align: center;
      padding-bottom: 20px;
      border-bottom: 1pt solid #efefef;
      .time {
        font-size: 15px;
        color: #eb5252;
        line-height: 20px;
      }
      .allPrice {
        font-size: 20px;
        font-weight: bold;
        line-height: 50px;
        color: #eb5252;
        span {
          font-size: 30px;
          font-weight: bold;
        }
      }
      .text {
        font-size: 13px;
        color: #999;
        line-height: 20px;
      }
    }
    .content-mid {
      .title {
        color: #999;
        margin: 20px 0 10px;
        font-size: 15px;
      }
      .radio-list {
        .radio-item {
          display: flex;
          align-items: center;
          height: 40px;
          line-height: 40px;
          img {
            width: 20px;
            margin-right: 15px;
          }
          .flex-2 {
            font-size: 15px;
            font-weight: bold;
            color: #333;
            flex: 2;
            span {
              color: #eb5252;
              font-weight: normal;
            }
            .symbol {
              font-size: 10px;
            }
          }
          .circle {
            border: 1pt solid #d2d2d2;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            box-sizing: border-box;
            div {
              background: #fff;
              width: 7px;
              height: 7px;
              margin: 3px auto;
              border-radius: 50%;
            }
          }
        }
      }
    }
  }

  .van-popup {
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    .btn {
      margin: 50px 15px 10px;
      background: #eb5252;
      height: 50px;
      line-height: 50px;
      color: #fff;
      text-align: center;
      font-size: 15px;
      border-radius: 50px;
    }
  }

  .popup_wrap {
    background: #fff;
    padding: 20px;
    width: 80vw;
    border-radius: 10px;
  }

  .popup_icon {
    height: 15px;
    img {
      width: 15px;
      height: 15px;
      float: right;
    }
  }

  .popup_content .title {
    font-weight: bold;
    font-size: 20px;
    line-height: 40px;
  }

  .popup_content .title span {
    color: #eb5252;
  }

  .popup_content .text {
    font-size: 15px;
    color: #999;
    line-height: 25px;
    margin: 10px 0;
    text-align: justify;
  }

  .popup_btns {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }

  .popup_btns .popup_btn {
    border: 1pt solid #eee;
    border-radius: 5px;
    height: 40px;
    line-height: 40px;
    font-size: 15px;
    color: #999;
    width: 125px;
    text-align: center;
  }

  .popup_btns .go {
    margin-left: 15px;
    border-color: #eb5252;
    background: #eb5252;
    color: #fff;
    flex: 2;
  }
}
</style>
<style lang="scss">
.buyActivityGoods {
  .van-popup {
    background: #fff;
  }
  .van-cell {
    padding: 10px;
  }
  .flex-2 {
    .van-cell {
      padding: 0;
    }
    .van-field__control {
      background: #fff;
    }
  }
  .van-swipe-cell__right {
    background: #eb5252;
    color: #fff;
    height: 120px;
    text-align: center;
    width: 65px;
    font-size: 15px;
  }
  .van-switch--on {
    background: $base-color;
  }
}
</style>
